import { useQuery } from "react-query";
import { fetchUserBookings } from "../../api/api";
import { ErrorAlert } from "../reusable_components/alerts/alerts";
import { LoadingComponent } from "../reusable_components/loading-content";
import { BookingCard } from "../reusable_components/booking_card";
import { Button } from "../reusable_components/custom_button";
import { cn } from "../../utils/custom_functions";
import { ROUTE_LINKS } from "../../utils/route_links";
import { LoadPageAnimation } from "../reusable_components/load_page_animation";

export default function Bookings() {
  // fetch bookings
  const {
    data: bookings,
    error: bookingsErrors,
    isError: isBookingsError,
    isLoading: isBookingsLoading,
  } = useQuery({
    queryKey: ["bookings"],
    queryFn: fetchUserBookings,
    onSuccess: (data) => {
      // console.log(data);
    },
  });

  if (isBookingsLoading) {
    return <LoadingComponent secondary_text={"loading your bookings"} />;
  }

  if (isBookingsError) {
    return <ErrorAlert secondaryText={`${bookingsErrors.response.data}`} />;
  }

  return (
    <LoadPageAnimation>
      <main className="max-w-xl mx-auto p-6 my-6 text-black bg-white">
        <div className="rounded-md p-6 shadow-sm border border-gray-200 my-6">
          <h2 className="text-start text-black px-2 font-extrabold text-3xl">
            Bookings
            <p className="text-sm font-normal text-slate-800 pt-2">
              Trust matols logistics services to transport your items safely,
              find your bookings below. dont have a booking with us?{" "}
              <a
                href={ROUTE_LINKS.createbooking}
                className="text-yellow-400 font-bold hover:opacity-75"
              >
                Book with us
              </a>
              .
            </p>
          </h2>
        </div>

        {/*if no bookings*/}
        {bookings?.results.length <= 0 && (
          <div className="min-h-72">
            <p className="text-lg text-center py-12 font-bold">
              No active or canceled bookings
            </p>
          </div>
        )}
        {/*if no bookings*/}
        {/*Put bookings here*/}
        <div className="flex flex-col gap-6">
          {bookings?.results.map((booking, index) => {
            return (
              <BookingCard key={index} booking={booking}>
                <div className="w-full flex flex-row justify-end">
                  <Button
                    disabled={
                      booking.booking_canceled || booking.booking_completed
                    }
                    className={cn("mt-6 w-auto hover:opacity-75 px-6", {
                      "bg-red-500 text-white": booking.booking_canceled,
                      "bg-gray-200 text-gray-600": booking.booking_completed,
                    })}
                    onClick={() => {
                      let anchor = window.document.createElement("a");
                      anchor.href = `/bookings/booking/${booking.id}/view/`;
                      anchor.target = "_blank";
                      anchor.rel = "noreferrer";
                      anchor.click();
                    }}
                  >
                    Edit booking
                  </Button>
                </div>
              </BookingCard>
            );
          })}
        </div>
        {/*Put bookings here*/}
      </main>
    </LoadPageAnimation>
  );
}
